
.dot {
  width: 1.4em;
  height: 1.4em;
  border-radius: 4px;
  border: 2px solid green;
  display: inline-block;
  margin: 3px;
  margin-bottom: 0;
}
.dotbar {
  width: 300px;
  margin-top: 3px;
  margin-bottom: 4px;
  margin-left: auto;
  margin-right: auto;
}
.ball {
  background: green;
}



