
.ftp {
  position: relative;
}
.numerand1 {
  background: orange;
}
.activen {
  background: blue;
  color: #fff;
}
.product {
  background: green;
  color: #fff;
}
.numerand2 {
  background: blue;
  color: #fff;
}
.numerandb {
  background: green;
  color: #ffffff;
}
.numerand00 {
  background: #fff;
  color: #fff;
}
.active {
  background: green;
  font-weight: bold;
  color: #fff;
}
.fheading {
  font-size: 40px;
  font-weight: bold;
  color: #fff;
  margin-top: 40px;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 24px;
}
.fheading DIV {
    width: 60px;
    margin: 0;
    border-radius: 6px;
    display: inline-block;
}
.fheading .large-operator {
    color: #000;
    width: 30px;
}
.fact-table {
  cursor: pointer;
  border: 1px solid #fff;
  margin-top: 30px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}
.fact-table .toperator {
    padding: 6px;
    border-right: 2px solid #333;
    font-weight: bold;
    font-size: 2em;
    line-height: 1;
}
.cell {
  width: 28px;
  height: 28px;
  display: inline-block;
  line-height: 28px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
}
.factr {
  width: 524px;
  margin-left: auto;
  margin-right: auto;
}



