
.answergrid {
  margin-top: 15px;
  margin-bottom: 14px;
}
.answergrid TABLE {
    border: none;
}
.answergrid TABLE TD {
      border: none;
      border-right: 1px solid #fff;
      border-bottom: 1px solid #fff;
}
.answergrid TABLE TD.vis-operator {
      font-weight: bold;
      font-size: 30px;
      line-height: 10px;
}



