
.xtitle {
  font-size: 60px;
  font-weight: bold;
  font-family: "Avenir Next";
}
.xtable {
  width: 350px;
  margin-left: auto;
  margin-right: auto;
}
.factrow {
  margin-bottom: 12px;
}
.cell {
  width: 34px;
  height: 34px;
  display: inline-block;
  line-height: 34px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
}
.numerand0 {
  color: #ffffff;
  background: #000;
}
.numerand1x {
  color: #ffffff;
  background: blue;
}



