
.dice {
  width: 120px;
  height: 120px;
  margin-left: auto;
  margin-right: auto;
  background: #fff;
  margin-top: 0;
  margin-bottom: 0;
  border: 3px solid #666;
  border-radius: 10px;
}
.dice TD {
    padding: 4px;
    background: #fff;
    border: 8px solid #fff;
}
.dice .d {
    background: blue;
}



