
.numerand0 {
  background: #000;
  color: #fff;
}
.numerand1 {
  background: orange;
}
.activen {
  background: blue;
  color: #fff;
}
.product {
  background: green;
  color: #fff;
}
.numerand2 {
  background: blue;
}
.active {
  background: green;
  font-weight: bold;
  color: #fff;
}
.fheading {
  font-size: 40px;
  font-weight: bold;
  color: #fff;
  margin-top: 40px;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 24px;
}
.fheading DIV {
    width: 60px;
    margin: 0;
    border-radius: 6px;
    display: inline-block;
}
.fheading .large-operator {
    color: #000;
    width: 30px;
}
.fact-table {
  cursor: pointer;
  width: 340px;
  border: 1px solid #fff;
  margin-top: 30px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}
.fact-table .toperator {
    padding: 6px;
    border-right: 2px solid #fff;
    font-weight: bold;
    font-size: 2em;
    line-height: 1;
}
.fact-table .col {
    border-right: 1px solid #fff;
}
.fact-table TABLE {
    width: 100%;
    border-collapse: collapse;
    border: 1px solid #fff;
}
.fact-table TABLE TD {
      font-size: 20px;
      font-weight: bold;
      padding-top: 4px;
      padding-bottom: 4px;
      border-bottom: 1px solid #fff;
      border-right: 1px solid #fff;
      width: 30px;
}



